<template>
  <div>
    <form class="formLesson form-inline container" @submit.prevent="clickSubmit()">
      <div class="userEmail mt-5">Welcone Back, {{userEmail}}</div>
      <div class="asssignedLessons mt-4">Assigned Lessons: </div>
      <div class="lessonNumber" v-for="item in assignedLessons" :key="item">
          <span @click="clickLessonNumber(item)" :class="{'lessonNumberList' :haveLessonLink}">{{item}}</span>
      </div>
         
      <div class="mt-4 enterLessonNumber">Please enter lesson to submit: 
        <input type="text" class="mb-3 form-control" id="lessonNumber" v-model="lessonNumber" @keyup="keyInput">
      </div>
      <div>
        <button type="submit" class="btn btn-secondary mt-5" :disabled="disabled" >Submit</button>
        <loading :active.sync="isLoading"></loading> 
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'UPLOAD-PANEL',
  data () {
    return {
      msg: 'Welcome to ChinesePod Upload Panel',
      lessonNumber: "",
      disabled: true,
      userEmail: "",
      assignedLessons: [],
      haveLessonLink: true,
      isLoading:false,
    }
  },
  methods:{
    clickSubmit(){      
      this.isLoading = true;
      this.currentLesson = this.lessonNumber.toUpperCase();        
      
      /* call API 
        if(success){
          // call 完API會取得一包資料要存到this.$store.state中, 給upload page 使用
          this.$router.push("/upload-page/");
        } 
        */
        // Need to change userRemindData to API data
        // 'submissionSurface': 'NO_CONTENT_YET',
        // 'submissionSurface': 'CONTENT_INCOMPLETE',
        // 'submissionSurface': 'CONTENT_OK',

      let userRemindData = {
         'submissionSurface': 'CONTENT_INCOMPLETE',
      };
      this.$store.state.userRemindData = userRemindData;
      this.$store.state.currentLesson = this.currentLesson;  
      // this.$router.push("/upload-page/" + this.currentLesson);   
      
      let self = this;
      setTimeout(function(){
        self.$router.push("/upload-page/" + self.currentLesson);         
      }, 1000)
    },
    keyInput() {
      this.submitDisabled();
    },
    submitDisabled(){
      if(this.lessonNumber == ""){
        this.disabled = true; 
      }else {
        this.disabled = false;        
      } 
    },
    clickLessonNumber(number){
      if(this.haveLessonLink == true){
        this.lessonNumber = number;
      }
      this.submitDisabled();
    }
  },
  created() {
    // this.$store.state.uploadCompleted = false;
    let userInfoPath = this.$route.path;
    this.userEmail = userInfoPath.split("+")[1];

    if(userInfoPath.split("+")[2] == undefined){
      this.assignedLessons.push("ANY lessons");
      this.haveLessonLink = false;
    }else if(userInfoPath.split("+")[2] == "none"){
      this.assignedLessons.push("Is no longer authorized");
      this.haveLessonLink = false;
    }else{
      for(var i = 2; i <= userInfoPath.length; i++){
        this.assignedLessons.push(userInfoPath.split("+")[i]);
        this.haveLessonLink = true;
      }
    }
  },
}
</script>

<style scoped>
div,p {
  font-size: 14px;
}
.formLesson {
  width: 100%;
  max-width: 600px;
  padding: 15px;
  margin: auto;
}
.userEmail, .asssignedLessons {
  font-weight: normal;
  margin: auto;
  width: 100%;
}
.form-control {
  width: 100%;
  border-radius: 5px;
  font-size: 14px;
  padding-left: 8px;
  margin-top: 10px;
}

/* .btn-primary {
  border-radius: 5px;
  background-color: #9E9E9E;
  border-color: #9E9E9E;
  letter-spacing: 2.1px;
  font-size: 12px;
} */

/* .btn-primary:not(:disabled):hover {
  background-color:#757575;
  border-color: #757575;
} */

.lessonNumberList {
  padding-right: 5px;
  color: #003399;

}
.lessonNumberList:hover {
  text-decoration: underline;
  cursor: pointer;
}
.enterLessonNumber {
  width: inherit;
}
</style>



