import Vue from 'vue'
import Router from 'vue-router'
import lessonNumber from '@/components/lessonNumber'
import UploadPage from '@/components/UploadPage'
import submissionReceived from '@/components/submissionReceived'

Vue.use(Router)

export default new Router({
  base: '/recap/upload-files',
  routes: [
    {
      path: '/',
      name: 'lessonNumber',
      component: lessonNumber,
      alias: '/authorize+*',
    },
    {
      path: '/upload-page/:id',
      name: 'UploadPage',
      component: UploadPage,
    },
    {
      path: '/submission-received',
      name: 'submissionReceived',
      component: submissionReceived,
    }
  ]
})





