<template>
  <div class="ml-5">
    <div class="SubmissionTitle mt-5">Submission Received</div>
    <div class="asssignedLessons mt-4">{{slidesNumber}} Slides received, complete in 4 versions No consistency errors detected.</div>
  </div>
</template>

<script>
export default {
  name: 'SUBMISSION-RECEIVED',
  data () {
    return {
        slidesNumber : 0
    }
  },
  methods:{

  },
  created() {
    //Get slides number
    this.slidesNumber = this.$store.state.slidesNumber;
    
    if (sessionStorage.getItem("store") ) {
      this.$store.replaceState(Object.assign({}, this.$store.state,JSON.parse(sessionStorage.getItem("store"))))
    }

    //在頁面重新整理時將vuex裡的資訊儲存到sessionStorage裡
    window.addEventListener("beforeunload",()=>{
      sessionStorage.setItem("store",JSON.stringify(this.$store.state))
    })
  },
}
</script>

<style scoped>

</style>



